import React from 'react';

const dropdownStyles = `
  padding: 10px;
  border: 1px solid #E5E5E5;
  border-top: none;
  width: 300px;
  background-color: #FFF;
  margin: 0 auto;
`;

export default function Dropdown({ children, onRequestClose }) {
  return (
      <div>
            <div class="modal absolute bg-white w-full h-full top-0 rounded-lg flex flex-col justify-center items-center bg-gray-800 bg-opacity-95 transition-all">
                {children}
                <button onClick={onRequestClose} className={'text-red-500 mt-5 px-3 py-2 rounded-lg hover:bg-gray-700'}>Cancel</button>
            </div>
      </div>
        
  );
}