import React from 'react';

export default function Button({ children, onClick }) {
  return (
    <div onClick={onClick} className={`px-5 select-none py-3 bg-gray-200 dark:bg-gray-900 text-white rounded-lg rounded-t-none text-center flex justify-center transition dark:hover:bg-gray-800 hover:bg-gray-300 cursor-pointer`}>
        <p className={`transition font-sans font-medium text-sm text-justify text-blue-600 dark:text-blue-400`}>Add to Calendar</p>
    </div>
  );
}